**************************************/\nhtml, body, div, span, applet, object, iframe,h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img,\nins, kbd, q, s, samp,small, strike, strong, sub, sup, tt, var,b, u, i, center,dl, dt, dd, ol, ul, li,fieldset, form, label, legend,table, caption,\ntbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section,\nsummary,time, mark, audio, video { margin: 0; padding: 0; border: 0; font-size: 100%; font: inherit; vertical-align: baseline; }\n/***********************************************\n* HTML5 display-role reset for older browsers\n***********************************************/\narticle, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section { display: block; }\nbody { line-height: 1; }\nol, ul { list-style: none; }\nblockquote, q { quotes: none; }\nblockquote:before, blockquote:after, q:before, q:after { content: ''; content: none; }\ntable { border-collapse: collapse; border-spacing: 0; }\n* { -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; outline: none; }\n","@import \"generycs/reset\";\n /* =============================================================================\n --  @project : Growlers Bar\n --  @theme   : growler\n --  @author  : Mariano Molina\n --  @date    : 2016-11-27\n ============================================================================= */\n@import \"generycs/vars\";\n@import \"generycs/mixins\";\n@import \"generycs/icons\";\n@import \"generycs/fonts\";\n@import \"generycs/generycs\";\n// =============================================================================\n// -- Main theme\n// =============================================================================\n@import \"components/header\";\n@import \"components/buttons\";\n@import \"components/searchbar\";\n@import \"components/product\";","// =============================================================================\n// ICONS\n// =============================================================================\n.ic {\n\tdisplay: inline-block;\n\n\t&_growler_logo {\n\t\tbackground: url(../img/Growlers-logo-desktop.svg) center center no-repeat;\n\t\t//@include size(230px, 27px);\n\t\t@include size(160px, 18px);\n\t}\n\n\t// header\n\t&_cart {\n\t\tbackground: url(../img/icons/ic_shopping_cart.svg) center center no-repeat;\n\t\t@include size(26px, 24px);\n\t\t\t//@include size(33px, 28px);\n\t}\n\n\t&_search {\n\t\tbackground: url(../img/icons/ic_search.svg) center center no-repeat;\n\t\t@include size(26px, 26px);\n\t}\n\n\n\t@media #{$breakpoint-smartphone-medium} {\n\t\t&_growler_logo {\n\t\t\t@include size(190px, 22px);\n\t\t}\n\t}\n\n\t@media #{$breakpoint-smartphone-big} {\n\t\t&_growler_logo {\n\t\t\t@include size(230px, 27px);\n\t\t}\n\t}\n}\n","// =============================================================================\n// Mixins\n// =============================================================================\n\n// =============================================================================\n// String Replace\n// =============================================================================\n@function str-replace($string, $search, $replace: \"\") {\n  $index: str-index($string, $search);\n\n  @if $index {\n    @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);\n  }\n\n  @return $string;\n}\n\n// =============================================================================\n// Font Face\n// =============================================================================\n@mixin font-face($name, $path, $weight: null, $style: null, $exts: ttf eot svg woff2 woff) {\n  $src: null;\n\n  $extmods: (\n    eot: \"?\",\n    svg: \"#\" + str-replace($name, \" \", \"_\")\n  );\n\n  $formats: (\n    otf: \"opentype\",\n    ttf: \"truetype\"\n  );\n\n  @each $ext in $exts {\n    $extmod: if(map-has-key($extmods, $ext), $ext + map-get($extmods, $ext), $ext);\n    $format: if(map-has-key($formats, $ext), map-get($formats, $ext), $ext);\n    $src: append($src, url(quote($path + \".\" + $extmod)) format(quote($format)), comma);\n  }\n\n  @font-face {\n    font-family: quote($name);\n    font-style: $style;\n    font-weight: $weight;\n    src: $src;\n  }\n}\n// =============================================================================\n// Border-box\n// =============================================================================\n@mixin border-box {\n  -webkit-box-sizing: border-box;\n     -moz-box-sizing: border-box;\n          box-sizing: border-box;\n}\n\n// =============================================================================\n// Border-radius\n// =============================================================================\n@mixin border-radius($radius) {\n  -webkit-border-radius: $radius;\n     -moz-border-radius: $radius;\n      -ms-border-radius: $radius;\n          border-radius: $radius;\n}\n\n// =============================================================================\n// Border-radius\n// =============================================================================\n@mixin animationDelay($duration, $delay) {\n  -webkit-animation-duration: $duration;\n     -moz-animation-duration: $duration;\n      -ms-animation-duration: $duration;\n          animation-duration: $duration;\n\n  -webkit-animation-delay: $delay;\n     -moz-animation-delay: $delay;\n      -ms-animation-delay: $delay;\n          animation-delay: $delay;\n}\n\n// =============================================================================\n// Transition\n// =============================================================================\n@mixin transition($time) {\n  transition:all $time linear;\n  -o-transition:all $time linear;\n  -moz-transition:all $time linear;\n  -webkit-transition:all $time linear;\n}\n\n// =============================================================================\n// Animation Delay\n// =============================================================================\n@mixin animationDelay($duration, $delay) {\n  -webkit-animation-duration: $duration;\n     -moz-animation-duration: $duration;\n      -ms-animation-duration: $duration;\n          animation-duration: $duration;\n\n  -webkit-animation-delay: $delay;\n     -moz-animation-delay: $delay;\n      -ms-animation-delay: $delay;\n          animation-delay: $delay;\n}\n\n// =============================================================================\n// Element size\n// =============================================================================\n@mixin size($width, $height) {\n  width: $width;\n  height: $height;\n}\n\n// =============================================================================\n// Border-box\n// =============================================================================\n@mixin placeholder($color) {\n  ::-webkit-input-placeholder {\n     color: $color;\n  }\n  :-moz-placeholder { /* Firefox 18- */\n     color: $color;\n  }\n  ::-moz-placeholder {  /* Firefox 19+ */\n     color: $color;\n  }\n  :-ms-input-placeholder {\n     color: $color;\n  }\n}\n\n// =============================================================================\n// Calculate rem helper\n// =============================================================================\n@function calculateRem($size) {\n  $remSize: $size / 16px;\n  @return $remSize * 1rem;\n}\n\n// =============================================================================\n// Rem font-size\n// =============================================================================\n@mixin font-size($size) {\n  font-size: $size;\n  font-size: calculateRem($size);\n}\n\n// =============================================================================\n// Rem line-height\n// =============================================================================\n@mixin line-height($size) {\n  line-height: $size;\n  line-height: calculateRem($size);\n}\n\n// =============================================================================\n// Clearfix helper\n// =============================================================================\n%clearfix {\n  *zoom: 1;\n  &:before,\n  &:after {\n    content: \"\";\n    display: table;\n  }\n  &:after {\n    clear: both;\n  }\n}\n\n// =============================================================================\n// LINE\n// =============================================================================\n@mixin line {\n  display: inline-block;\n  width: $button-size;\n  height: 5px;\n  vertical-align: middle;\n  background: $orange;\n  transition: $button-transition;\n\n  @media #{$breakpoint-smartphone-big} {\n    width: $button-size-mobile;\n  }\n}\n\n// =============================================================================\n// FLEX-LIST\n// =============================================================================\n@mixin flex-list {\n  display: flex;\n  flex-flow: row;\n  flex-wrap: nowrap;\n  align-items: center;\n  justify-content: center;\n}\n\n// =============================================================================\n// Opacity\n// =============================================================================\n@mixin opacity($opacity) {\n  opacity: $opacity;\n  $opacity-ie: $opacity * 100;\n  filter: alpha(opacity=$opacity-ie); //IE8\n}\n","// =============================================================================\n// Variables\n// =============================================================================\n$button-size: 2.5rem;\n$button-size-mobile: 2.5rem;\n$button-transition: .3s;\n\n// =============================================================================\n// -- FONTS\n// =============================================================================\n$gothamLight  : 'Gotham-Light';\n$gothamBook   : 'Gotham-Book';\n$gothamMedium : 'Gotham-Medium';\n$gothamBold   : 'Gotham-Bold';\n// =============================================================================\n// -- COLORES\n// =============================================================================\n$white: #FFFFFF;\n$black: #000000;\n$gray: #6c6c6c;\n\n// theme\n$orange: #ff9b19;\n\n// =============================================================================\n// -- BREAKPOINTS\n// =============================================================================\n$breakpoint-small: 'only screen and (max-width: 20em)';\n$breakpoint-medium: 'only screen and (min-width: 49em)';\n//desktop\n$breakpoint-desktop-big: '(min-width: 106.250em)';\n$breakpoint-desktop-medium: '(min-width: 92em)';\n// laptop\n$breakpoint-laptop-big: '(min-width: 90em)';\n$breakpoint-laptop-pc: '(min-width: 85em)';\n$breakpoint-laptop-small: '(min-width: 70.000em)';\n// tablet\n$breakpoint-tablet-land:'(min-width: 63.938em)';\n$breakpoint-tablet-port:'(min-width: 47em)';\n// smartphone landscape\n$breakpoint-smartphone-big-landscape: 'only screen and (min-width: 40em)';\n$breakpoint-smartphone-medium-landscape: 'only screen and (min-device-width: 41.688em)';\n$breakpoint-smartphone-small-landscape: 'only screen and (max-device-width: 35.500em)';\n// smartphone portrait\n$breakpoint-smartphone-big: '(min-width: 25.000em)';\n$breakpoint-smartphone-medium: '(min-width: 23.375em)';\n$breakpoint-smartphone-small: '(max-width: 20.000em)';\n\n\n","// =============================================================================\n// - Generycs elements\n// =============================================================================\n*, *:after, *:before {\n  margin: 0;\n  padding: 0;\n  @include border-box;\n  @include transition(2ms);\n}\n\nbody {\n  background-color: $white;\n  font-family: 'Helvetica Neue', sans-serif;\n  color: $black;\n}\n\n#wrapper {\n  @include size(100%, auto);\n  overflow: hidden;\n  margin-top: 56px;\n\n  @media #{$breakpoint-smartphone-big} {\n    margin-top: 66px;\n  }\n}","// =============================================================================\n// - header element\n// =============================================================================\n#header {\n\t@include size(100%, 56px);\n\tbackground-color: $black;\n\t//overflow: hidden;\n\tpadding: 8px;\n\t// position\n\tposition: fixed;\n\ttop: 0;\n\tright: 0;\n\tleft: 0;\n\tz-index: 10;\n\t// flex\n\tdisplay: flex;\n\tflex-direction: row;\n\tflex-wrap: nowrap;\n\talign-items: center;\n\tjustify-content: space-between;\n\n\t// Logo\n\t& > .logo {\n\t\tdisplay: inline-block;\n\t\tmargin: auto;\n\t\tmargin-left: 0px;\n\t\tmargin-right: 10px;\n\t}\n\n\t// =============================================================================\n\t// - Responsive\n\t// =============================================================================\n\t//@media #{$breakpoint-tablet-port} {}\n\t//@media #{$breakpoint-tablet-land} {}\n\t@media #{$breakpoint-smartphone-big} {\n\t\t@include size(100%, 66px);\n\t\tpadding: 16px 12px;\n\t}\n}\n\n// =============================================================================\n// - header-nav\n// =============================================================================\n.header-nav {\n\t@include size(100%, auto);\n\tmargin: auto;\n\tmargin-right: 0px;\n\t//text\n\tcolor: $white;\n\t// flex\n\tdisplay: flex;\n\tflex-direction: row;\n\tflex-wrap: nowrap;\n\talign-items: center;\n\tjustify-content: space-between;\n\n\n\t& > .btn_lines {\n\t\tmargin: auto;\n\t\tmargin-right: 0px;\n\t}\n}\n\n.user-info {\n\tmargin: auto;\n\n\t.login {\n\t\t//@include size(30px, 30px);\n\t\t@include size(26px, 26px);\n\t\tbackground: url(../img/icons/ic_user.svg) center center no-repeat;\n\t\tdisplay: block;\n\t\t// texts\n\t\ttext-indent: -9999px;\n\t\ttext-decoration: none;\n\t}\n}\n\n.blockcart {\n\tmargin: auto;\n\n\t.header {\n\t\t& > a {\n\t\t\tdisplay: block;\n\t\t\tposition: relative;\n\t\t\toverflow: hidden;\n\t\t\t//\n\t\t\ttext-decoration: none;\n